body {
  background-color: var(--white);
  color: var(--black);
}

.App {
  align-items: center;
  display: flex;
  flex-direction: column;
  margin: auto;
  max-width: 600px;
  text-align: center;
  /* height: 100vh; */
  max-height: 1000px;

  height: var(--app-height);
}

.App-iOS {
  height: -webkit-fill-available;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  max-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.hint {
  font-size: 1.5rem;
  margin-top: 12px;
  margin: 24px 0px;
  margin-top: 12px;
  padding: 0 12px;
  margin-bottom: 20px;
}

.bar {
  min-width: 0 !important;
}

.message {
  margin-top: 12px;
  color: white;
  position: fixed;
  bottom: 0px;
  margin-bottom: 205px;
  height: 50px;
  width: fit-content;
  padding: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
}

.slide-top {
  -webkit-animation: slide-top 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
  animation: slide-top 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}

